'use client';

import { motion } from "framer-motion";
import { Badge } from "@/components/ui/badge";
import { Code2, Cpu, Globe, GraduationCap, Layout, Server } from "lucide-react";

const skills = [
    { name: "JavaScript (ES6+)", icon: <Code2 className="w-4 h-4" /> },
    { name: "TypeScript", icon: <Cpu className="w-4 h-4" /> },
    { name: "React", icon: <Layout className="w-4 h-4" /> },
    { name: "Next.js", icon: <Globe className="w-4 h-4" /> },
    { name: "Node.js", icon: <Server className="w-4 h-4" /> },
    { name: "NestJS", icon: <Server className="w-4 h-4" /> },
    { name: "PostgreSQL", icon: <Server className="w-4 h-4" /> },
    { name: "Tailwind CSS", icon: <Layout className="w-4 h-4" /> },
    { name: "Git", icon: <Code2 className="w-4 h-4" /> },
];

export function About() {
    return (
        <section id="about" className="py-24 relative overflow-hidden">
            <div className="container mx-auto px-4 md:px-6">
                <div className="flex flex-col md:flex-row justify-between items-end mb-12 gap-4">
                    <div className="max-w-xl">
                        <h2 className="text-3xl md:text-5xl font-bold tracking-tight mb-4">About Me</h2>
                        <p className="text-muted-foreground text-lg">
                            Passionate about building software that solves real-world problems.
                        </p>
                    </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                    {/* Bio Card */}
                    <motion.div
                        whileHover={{ y: -5 }}
                        className="md:col-span-2 p-8 glass-morphism flex flex-col justify-between"
                    >
                        <div>
                            <div className="p-3 w-fit rounded-xl bg-blue-500/10 text-blue-400 mb-6">
                                <GraduationCap className="w-6 h-6" />
                            </div>
                            <h3 className="text-2xl font-bold mb-4 text-white">My Journey</h3>
                            <p className="text-muted-foreground leading-relaxed mb-6">
                                I am a passionate Full Stack Developer with experience in building web applications spanning across various domains.
                                My journey in tech has been driven by a constant desire to learn and innovate.
                                I specialize in crafting efficient, scalable, and user-friendly solutions that meet business needs.
                            </p>
                        </div>
                        <div className="flex gap-4">
                            <div className="text-sm font-medium px-4 py-2 rounded-lg bg-white/5 border border-white/10">3+ Years Experience</div>
                            <div className="text-sm font-medium px-4 py-2 rounded-lg bg-white/5 border border-white/10">20+ Projects Completed</div>
                        </div>
                    </motion.div>

                    {/* Technical Skills Bento */}
                    <motion.div
                        whileHover={{ y: -5 }}
                        className="p-8 glass-morphism border-blue-500/20 shadow-[0_0_30px_rgba(56,189,248,0.05)]"
                    >
                        <h3 className="text-2xl font-bold mb-6 text-white text-gradient">Technical Arsenal</h3>
                        <div className="flex flex-wrap gap-2">
                            {skills.map((skill) => (
                                <motion.div
                                    key={skill.name}
                                    whileHover={{ scale: 1.05 }}
                                    className="flex items-center gap-2 px-3 py-1.5 rounded-lg bg-white/5 border border-white/10 text-sm hover:border-blue-500/50 hover:bg-blue-500/10 transition-all cursor-default"
                                >
                                    {skill.icon}
                                    <span>{skill.name}</span>
                                </motion.div>
                            ))}
                        </div>
                    </motion.div>

                    {/* Small Extra Bento Card */}
                    <motion.div
                        whileHover={{ y: -5 }}
                        className="p-8 glass-morphism flex items-center gap-6"
                    >
                        <div className="flex-1">
                            <h4 className="font-bold text-white mb-1">Architecture</h4>
                            <p className="text-xs text-muted-foreground">Clean, modular, and scalable codebases.</p>
                        </div>
                        <div className="w-12 h-12 rounded-full border border-white/10 flex items-center justify-center bg-blue-500/10">
                            <Code2 className="w-6 h-6 text-blue-400" />
                        </div>
                    </motion.div>

                    {/* Another Extra Bento Card */}
                    <motion.div
                        whileHover={{ y: -5 }}
                        className="p-8 glass-morphism flex items-center gap-6"
                    >
                        <div className="flex-1">
                            <h4 className="font-bold text-white mb-1">UI/UX Focus</h4>
                            <p className="text-xs text-muted-foreground">Designing for users, optimized for performance.</p>
                        </div>
                        <div className="w-12 h-12 rounded-full border border-white/10 flex items-center justify-center bg-purple-500/10">
                            <Layout className="w-6 h-6 text-purple-400" />
                        </div>
                    </motion.div>

                    {/* Backend Card */}
                    <motion.div
                        whileHover={{ y: -5 }}
                        className="p-8 glass-morphism flex items-center gap-6"
                    >
                        <div className="flex-1">
                            <h4 className="font-bold text-white mb-1">Backend Mastery</h4>
                            <p className="text-xs text-muted-foreground">Robust APIs and efficient database management.</p>
                        </div>
                        <div className="w-12 h-12 rounded-full border border-white/10 flex items-center justify-center bg-emerald-500/10">
                            <Server className="w-6 h-6 text-emerald-400" />
                        </div>
                    </motion.div>
                </div>
            </div>
        </section>
    );
}
